<template>
    <div class="public_content">
        <div class="public_title">
          <span>商铺录入房源</span>
          <el-button class="fr" icon="el-icon-back" size="small" @click='$router.push({name: "storelists"})'>房源库</el-button>

        </div> 
        <el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="100px" class="demo-ruleForm">



            <el-form-item label="小区名称" prop="community_id">
              <el-select
                @blur='blurMethod'
                style="width: 250px;margin-right:20px;"
                v-model="ruleForm.community_id"
                filterable
                remote
                reserve-keyword
                placeholder="请输入关键词"
                :remote-method="remoteMethod"
                :loading="loading"> 
                <el-option
                  v-for="item in community_id"
                  :key="item.id"
                  :label="item.communityname"
                  :value="item.id">
                </el-option>
              </el-select>  
                <el-button type="primary" round v-show="community_id.length == 0 && temp" @click="addCommunity">录入小区</el-button>
            </el-form-item>
 


          <el-form-item label="商铺性质" prop="shopnature">
                <el-radio-group v-model="ruleForm.shopnature" >
                    <el-radio :label="0">出租</el-radio>
                    <el-radio :label="1">出售</el-radio>
                    <el-radio :label="2">转让</el-radio>
                </el-radio-group>
            </el-form-item>


            <el-form-item label="商铺情况" prop="shoptype" class="inlineBlock">
                <el-select v-model="ruleForm.shoptype" placeholder="商铺类型"  style="width: 120px;">
                    <el-option
                      v-for="item in shoptype"
                      :key="item.id"
                      :label="item.paramname"
                      :value="item.id">
                    </el-option>
                </el-select> 
            </el-form-item>

            <el-form-item prop="franchising" label-width="0" class="inlineBlock">
                <el-select v-model="ruleForm.franchising" placeholder="经营行业" style="width: 120px;">
                    <el-option
                      v-for="item in franchising"
                      :key="item.id"
                      :label="item.paramname"
                      :value="item.id">
                    </el-option>
                </el-select> 
            </el-form-item>
            <el-form-item prop="towards" label-width="0" class="inlineBlock">
                <el-select v-model="ruleForm.towards" placeholder="房屋朝向" style="width: 120px;">
                    <el-option
                      v-for="item in aspect"
                      :key="item.id"
                      :label="item.paramname"
                      :value="item.id">
                    </el-option>
                </el-select>
            </el-form-item>
             <el-form-item prop="is_division" label-width="0" class="inlineBlock">
               <el-select v-model="ruleForm.is_division" placeholder="是否分割" style="width: 120px;">
                    <el-option
                      v-for="item in is_division"
                      :key="item.id"
                      :label="item.paramname"
                      :value="item.id">
                    </el-option>
                </el-select>   
            </el-form-item>

        
             <el-form-item prop="face_width" label-width="0" class="inlineBlock">
                <el-input style="width:100px;" v-model="ruleForm.face_width"><span slot="suffix">m</span><span slot="prefix">面宽</span></el-input>
            </el-form-item>

            <el-form-item prop="floor_height" label-width="0" class="inlineBlock">
                <el-input style="width:100px;" v-model="ruleForm.floor_height"><span slot="suffix">m</span><span slot="prefix">层高</span></el-input>
            </el-form-item>

            <el-form-item prop="depth" label-width="0" class="inlineBlock">
                <el-input style="width:100px;" v-model="ruleForm.depth"><span slot="suffix">m</span><span slot="prefix">进深</span></el-input>
            </el-form-item>




          <div>
              <el-form-item label="面积租金" prop="area" class="inlineBlock">
                  <el-input style="width:150px;" v-model="ruleForm.area"><span slot="suffix">㎡</span></el-input>
              </el-form-item>
              <el-form-item label-width="0" class="inlineBlock" prop="price">
                  <el-input style="width:150px;" v-model="ruleForm.price" placeholder="0元表示面议"><span slot="suffix">元/月</span></el-input>
              </el-form-item>
              <el-form-item label-width="0" class="inlineBlock" prop="pay_type">
                  <el-select v-model="ruleForm.pay_type " placeholder="付款方式"  style="width: 120px;">
                  <el-option
                    v-for="item in paytype"
                    :key="item.id"
                    :label="item.paramname"
                    :value="item.id">
                  </el-option>
              </el-select> 
              </el-form-item>
                
            </div>

            
            
            <el-form-item label="房屋标题" prop="title">
                <el-input style="width:300px;" v-model="houseTitle"></el-input>
            </el-form-item>

            <el-form-item label="封面图片" prop="thumb"> 
               
              <upload-img  v-if="dataSuccess" :data='ruleForm.thumb' @onSuccess="changeLogo($event,'thumb')" style="display: inline-block"></upload-img>

              <span class="public_littleTip"><i class="iconfont">&#xe670;</i>单图，供列表页使用</span>

               
            </el-form-item>

            <el-form-item label="房源图片" prop="images">
               <upload-imgs  v-if="dataSuccess" :data='ruleForm.images' @onSuccess="changeImages"></upload-imgs>
            </el-form-item> 

            <el-form-item label="特色标签" prop="tag">
                <el-checkbox-group v-model="ruleForm.tag">
                    <el-checkbox v-for="item in tag" :label="item.id" :key="item.id">
                        {{ item.paramname }}
                    </el-checkbox>
                </el-checkbox-group>
            </el-form-item>

             <el-form-item label="配套设施" prop="matching">
                 <el-checkbox v-model="checkAll" @change="handleCheckAllChange" style="display: inline-block">全选</el-checkbox>

                <el-checkbox-group v-model="ruleForm.matching" @change="handleCheckedListsChange" style="display: inline-block;margin-left: 30px;">
                    <el-checkbox v-for="item in matching" :label="item.id" :key="item.id">
                        {{ item.paramname }}
                    </el-checkbox> 
                </el-checkbox-group>
            </el-form-item> 

            <el-form-item label="房屋描述" prop="description" style="margin-bottom: 120px;">
               <editor :value="ruleForm.description" 
                        @show="editors">
              </editor> 
              <!-- <el-input style="width: 700px" type="textarea" :rows="6"  v-model="ruleForm.description">
              </el-input> -->
            </el-form-item> 

            <el-button type="primary" round @click="submitForm('ruleForm')" class="public_submit">确认提交</el-button>
         </el-form>
    </div>
</template>

<script>
import { shopPublish, getCommunity } from "@/api/house";
import uploadImg from "@/components/upload/uploadimg";
import uploadImgs from "@/components/upload/uploadimgs";
import uploadVideo from "@/components/upload/uploadVideo";
import editor from "@/components/tinymce/editor";

export default {
  components: {
    uploadImg,
    uploadImgs,
    uploadVideo,
    editor
  },
  data() {
    return {
      dataSuccess: false,
      checkAll: false,
      temp: "", 
      community_id: [], // 小区列表
      loading: false,
      aspect: [],
      paytype: [],
      shoptype: [],
      franchising: [],
      certificate: [
        {
          id: 0,
          paramname: "不满两年"
        },
        {
          id: 1,
          paramname: "满两年"
        },
        {
          id: 2,
          paramname: "满五年"
        }
      ],
      is_division: [
        {
          id: 0,
          paramname: "否"
        },
        {
          id: 1,
          paramname: "是"
        }
      ],
      tag: [],
      matching: [],
      ruleForm: {
        community_id: "",
        price: "",
        area: "",
        shoptype: "",
        towards: "",
        thumb: "",
        images: [], // 房源图片
        title: "",
        description: "",
        shopnature: "",
        pay_type: "",
        is_division: "",
        face_width: "",
        floor_height: "",
        depth: "",
        franchising: "",
        matching: [],
        tag: []
      },
      rules: {
        community_id: [
          { required: true, message: "请输入小区名称", trigger: "change" }
        ],
        shopnature: [{ required: true, message: "请选择商铺性质" }],
        shoptype: [{ required: true, message: "请选择商铺类型" }],
        // towards: [{ required: true, message: "请选择房屋朝向" }],
        franchising: [{ required: true, message: "请经营行业" }],
        is_division: [{ required: true, message: "请选择" }],
        depth: [{ required: true, message: "请输入进深" }],
        face_width: [{ required: true, message: "请输入面框" }],
        area: [{ required: true, message: "请输入面积" }],
        // title: [{ required: true, message: "请输入房屋标题" }],
        price: [{ required: true, message: "请输入租金" }],
        floor_height: [{ required: true, message: "请输入层高" }],
        pay_type: [{ required: true, message: "请选择付款方式" }]
      }
    };
  },

  created() {
    if (this.$route.params.search) {
      this.remoteMethod(this.$route.params.search);
    }

    if (this.$route.query.id) {
      shopPublish("get", this.$route.query.id).then(res => {
        if (res.data.code == 1) {
          let data = res.data.data.agent;
          this.remoteMethod(data.communityname).then(res => {
            this.ruleForm = data;
            this.ruleForm.matching = data.matching.map(item => {
              return Number(item);
            });

            if (this.ruleForm.matching.length == this.matching.length) {
              this.checkAll = true;
            }

            this.ruleForm.tag = data.tag.map(item => {
              return Number(item);
            });
            this.dataSuccess = true;
          });
        }
      });
    } else {
      this.dataSuccess = true;
    }

    shopPublish("get").then(res => {  
      this.tag = res.data.data.tag;
      this.aspect = res.data.data.aspect;
      this.paytype = res.data.data.paytype;
      this.franchising = res.data.data.franchising;
      this.shoptype = res.data.data.shoptype;
      this.matching = res.data.data.matching ? res.data.data.matching : [];
    });
  },
  computed: {
    shopnature() {
      if (this.ruleForm.shopnature === 0) {
        return "出租";
      } else if (this.ruleForm.shopnature == 1) {
        return "出售";
      } else if (this.ruleForm.shopnature == 2) {
        return "转让";
      } else {
        return "";
      }
    },
    houseName() {
      let r = "";
      if (this.ruleForm.community_id) {
        this.community_id.forEach(element => {
          if (element.id == this.ruleForm.community_id) {
            r = element.communityname;
          }
        });
        return r;
      } else {
        return "";
      }
    },
    houseTitle: {
      set: function(value) {
        this.ruleForm.title = value;
      },
      get: function() {
        if (!this.$route.query.id) {
          return (
            this.shopnature +
            " " +
            this.houseName +
            " " +
            this.ruleForm.area +
            (this.ruleForm.area ? "平米" : "") +
            " " +
            this.ruleForm.price +
            (this.ruleForm.price ? "元/月" : "")
          );
        } else {
          return this.ruleForm.title;
        }
      }
    }
  },
  methods: {
    /* 搜索小区 */
    remoteMethod(query) {
      return new Promise((resolve, reject) => {
        this.temp = query;
        if (query) {
          this.loading = true;
          getCommunity(query)
            .then(res => {
              this.community_id = res.data.data_like;
              if (this.$route.params.search) {
                this.ruleForm.community_id = this.community_id[0].id;
              }
              resolve(res);
            })
            .catch(error => {
              reject(error);
            });

          this.loading = false;
        } else {
          this.community_id = [];
          resolve();
        }
      });
    },
    blurMethod(e) {
      if (this.community_id.length == 0) {
        this.ruleForm.community_id = this.temp;
      }
    },
    /* 添加小区 */
    addCommunity() {
      /* 1 esf  2 rent 3 store */
      this.$router.push({
        name: "addcommunity",
        params: { type: 3, search: this.temp }
      });
    },
    submitForm(formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          this.ruleForm.title = this.houseTitle;
          shopPublish("post", "", this.ruleForm).then(res => {
            if (res.data.code == 1) {
              if (this.$route.query.id) {
                this.$message.success("编辑成功");
              } else {
                this.$message.success("新增成功");
              }

              this.$router.push({ name: "storelists" });
            }
          });
        } else {
          return false;
        }
      });
    },
    changeLogo(data, key) {
      this.ruleForm[key] = data;
    },
    changeImages(data) {
      this.ruleForm.images = data;
    },
    editors(content) { 
      this.ruleForm.description = content;
    }, 
    handleCheckAllChange(val) {
      if (val) {
        this.ruleForm.matching = this.matching.map(item => {
          return Number(item.id);
        });
      } else {
        this.ruleForm.matching = [];
      }
    },
    handleCheckedListsChange(value) {
      let checkedCount = value.length;
      this.checkAll = checkedCount === this.matching.length;
    }
  }
};
</script>

<style lang='scss' scoped>
</style>



